.working-process{
    width: 100%;
    padding: 140px 0;
}
.working-process__cont{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 110px;
}
.working-process__item{
    padding: 50px 45px 0;
    width: calc(25% - 22px);
    height: 260px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 5px 5px 50px 5px #d3d3d3;
}
.working-process__item_img{
    width: 40px;
}
.working-process__item_title{
    font-size: 20px;
    font-weight: 600;
    color: #e1eefd;
    margin: 20px 0;
    text-transform: capitalize;
    text-align: center;
}
.working-process__item_subtitle{
    font-size: 15px;
    font-weight: 500;
    color: #e1eefd;
    text-align: center;
    line-height: 1.5;
}
.working-process__item_but{
    width: 230px;
    height: 55px;
    background-color: white;
    border: none;
    border-radius: 50px;
    font-size: 16px;
    font-weight: 500;
    color: black;
    text-transform: capitalize;
    transform: translateY(50%);
    cursor: pointer;
    outline: none;
}

/* 
response 
*/

@media screen and (max-width: 992px) {
    .working-process__cont {
        flex-wrap: wrap;
    }
    .working-process__item{
        width: calc(50% - 22px);
        justify-content: flex-end;
    }
    .working-process__item:nth-child(3),
    .working-process__item:nth-child(4){
        margin-top: 44px;
    }
}
@media screen and (max-width: 755px) {
    .working-process{
        padding: 70px 0;
    }
}
@media screen and (max-width: 705px) {
    .working-process__cont{
        margin-top: 70px;
    }
}
@media screen and (max-width: 600px) {
    .working-process__item{
        width: 100%;
    }
    .working-process__item:nth-child(2){
        margin-top: 44px;
    }
}