.icons{
    display: flex;
    flex-direction: column;
}
.icons__item{
    width: 105px;
    height: 105px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(248, 248, 248);
    margin-top: 10px;
    transition: box-shadow ease .4s;
}
.icons__item:first-child{
    margin-top: 0;
}
.icons__item:hover{
    box-shadow: 5px 5px 30px 5px #e2e2e2;
}
.icons__item_img{
    transform: translate(8px, 5px);
}

/* 
response 
*/

@media screen and (max-width: 992px) {
    .services__cont{
        flex-wrap: wrap;
    }
    .icons{
        width: 100%;
        flex-direction: row;
        justify-content: center;
        margin-bottom: 50px;
    }
    .icons__item{
        margin: 0 20px 0 0;
    }
    .icons__item:last-child{
        margin: 0;
    }
}
@media screen and (max-width: 550px) {
    .icons{
        width: 230px;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .icons__item{
        margin: 0;
    }
    .icons__item:nth-child(3),
    .icons__item:nth-child(4){
        margin-top: 20px;
    }
}